}
25%{-webkit-transform:rotate(5deg);transform:rotate(5deg)
}
30%{-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
}
@keyframes bellring{
0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
5%{-webkit-transform:rotate(-10deg);transform:rotate(-10deg)
}
9%{-webkit-transform:rotate(25deg);transform:rotate(25deg)
