.vue-dialog{border-radius:4px
}
.vue-dialog div{-webkit-box-sizing:border-box;box-sizing:border-box
}
.vue-dialog .dialog-flex{width:100%;height:100%
}
.vue-dialog .dialog-content{-webkit-box-flex:1;-ms-flex:1 0 auto;flex:1 0 auto;width:100%;padding:40px 20px;text-align:center
}
.vue-dialog .dialog-c-title{font-size:18px;font-weight:500
}
.vue-dialog .dialog-c-text{padding-top:16px;font-size:14px;line-height:1.5;color:#959595
}
.vue-dialog .dialog-c-text a{text-decoration:underline
}
.vue-dialog .dialog-c-text strong{display:block;color:#000;font-weight:400
}
.vue-dialog .dialog-c-list{display:inline-block;margin-top:16px
}
.vue-dialog .dialog-c-list li{text-align:left
}
.vue-dialog .dialog-c-list li:before{content:'';display:inline-block;margin-right:4px;width:2px;height:2px;border-radius:50%;background-color:#959595;vertical-align:middle
}
.vue-dialog .vue-dialog-buttons{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-flex:0;-ms-flex:0 1 auto;flex:0 1 auto;width:100%
}
.vue-dialog .vue-dialog-buttons-none{width:100%;padding-bottom:15px
}
.vue-dialog .vue-dialog-buttons a,.vue-dialog .vue-dialog-buttons button{padding:16px 0;height:56px
}
.vue-dialog .vue-dialog-buttons a:last-of-type,.vue-dialog .vue-dialog-buttons button:last-of-type{border-left:0
}
.vue-dialog .vue-dialog-button{background:transparent;padding:0;margin:0;border:1px solid #3617ce;cursor:pointer;-webkit-box-sizing:border-box;box-sizing:border-box;line-height:56px;height:56px;font:inherit;outline:none;color:#3617ce
}
.vue-dialog .vue-dialog-button:last-of-type{border:0;color:#fff;background-color:#3617ce
}
.vue-modal-resizer{display:block;overflow:hidden;position:absolute;width:12px;height:12px;right:0;bottom:0;z-index:9999999;background:transparent;cursor:se-resize
}
.vue-modal-resizer::after{display:block;position:absolute;content:'';background:transparent;left:0;top:0;width:0;height:0;border-bottom:10px solid #ddd;border-left:10px solid transparent
}
.vue-modal-resizer.clicked::after{border-bottom:10px solid #369be9
}
.v--modal-block-scroll{overflow:hidden;width:100vw
}
.v--modal-overlay{position:fixed;-webkit-box-sizing:border-box;box-sizing:border-box;left:0;top:0;width:100%;height:100%;background:rgba(0,0,0,0.8);z-index:999;opacity:1
}
.v--modal-overlay.scrollable{height:100%;min-height:100vh;overflow-y:auto;-webkit-overflow-scrolling:touch
}
.v--modal-overlay.scrollable .v--modal-box{margin-bottom:2px
}
.v--modal-overlay .v--modal-background-click{width:100%;height:100%
}
.v--modal-overlay .v--modal-box{position:relative;overflow:hidden;-webkit-box-sizing:border-box;box-sizing:border-box;max-height:100%
}
.v--modal{background-color:#fff;text-align:left;padding:0;border-radius:4px
}
.v--modal.v--modal-fullscreen{width:100vw;height:100vh;margin:0;left:0;top:0
}
.v--modal-top-right{display:block;position:absolute;right:0;top:0
}
.overlay-fade-enter-active,.overlay-fade-leave-active{-webkit-transition:all 0.2s;transition:all 0.2s
}
.overlay-fade-enter,.overlay-fade-leave-active{opacity:0
}
.nice-modal-fade-enter-active,.nice-modal-fade-leave-active{-webkit-transition:all 0.4s;transition:all 0.4s
}
.nice-modal-fade-enter,.nice-modal-fade-leave-active{opacity:0;-webkit-transform:translateY(-20px);transform:translateY(-20px)
}
[class^=popup] header{position:relative
}
[class^=popup] .btn-close{position:absolute;top:4px;right:4px;width:48px;height:48px;display:inline-block;vertical-align:middle;background-image:url(/static/img/icons.3f8a23b.svg);background-size:350px auto;background-repeat:no-repeat;background-position:-100px -50px
}
.popup header{padding:32px 20px 8px;background-color:#fff
}
.popup header h1{font-size:18px;line-height:28px;letter-spacing:-.12px;font-weight:500
}
.popup .layer-cont{padding:40px 20px;background-color:#fff
}
.popup .layer-cont .desc{margin-top:16px;font-size:14px;line-height:22px;letter-spacing:-.08px;color:#787878
}
.popup footer{background-color:#fff
}
.popup footer.btn-wrap .btn-line,.popup footer.btn-wrap .btn-solid{border-radius:0
}
.popup-full{height:100%
}
.popup-full header{min-height:56px;background-color:#fff
}
.popup-full header h1{padding:13px 52px 13px 20px;font-size:20px;line-height:30px;letter-spacing:-.14px
}
.popup-full header h1:after{content:'';position:absolute;left:0;bottom:-12px;width:100%;height:12px;-webkit-box-shadow:inset 0 1px 0 0 rgba(0,0,0,0.04),inset 0 4px 8px 0 rgba(0,0,0,0.04);box-shadow:inset 0 1px 0 0 rgba(0,0,0,0.04),inset 0 4px 8px 0 rgba(0,0,0,0.04);z-index:1
}
.popup-full .layer-cont{overflow-y:auto;-webkit-overflow-scrolling:touch;height:calc(100% - 56px);padding:24px 20px 20px;background-color:#fff
}
.list-service{font-size:0;text-align:center;border:1px solid #d0d0d0
}
.list-service li{display:inline-block;width:calc(100% / 3);font-size:14px;line-height:20px;text-align:center;border-left:1px solid #D0D0D0
}
.list-service li:first-of-type{border-left:none
}
.list-service a{display:block;padding:16px 0
}
.list-service [class^="ico-"]{width:48px;height:48px;display:block;vertical-align:middle;background-image:url(/static/img/icons.3f8a23b.svg);background-size:350px auto;background-repeat:no-repeat;background-position:0 -125px;margin:0 auto 4px
}
.list-service .ico-program{background-position:-150px -100px
}
.list-service .ico-experience{background-position:-200px -100px
}
.list-service .ico-consult{background-position:-250px -100px
}
.popup-full.login .layer-cont{padding:24px 32px 20px
}
.pop-main.v--modal-overlay .v--modal-box{height:100% !important;background-color:transparent
}
.pop-main .popup{position:relative;width:100%;height:100%;padding:40px 0 80px
}
.pop-main .layer-cont{overflow-y:auto;-webkit-overflow-scrolling:touch;position:relative;left:50%;top:50%;max-height:100%;padding:0;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)
}
.pop-main .layer-foot{position:absolute;bottom:40px;left:0;width:100%;margin-top:22px
}
.pop-main .layer-foot .txt-chk{display:block;line-height:20px;color:#bcbcbc
}
.pop-main .layer-foot .txt-chk input[type="checkbox"]{background-color:transparent
}
.pop-main .layer-foot .txt-chk input[type="checkbox"]:checked{background-color:#3617ce
}
.pop-main .layer-foot .txt-chk label{margin-top:1px;font-size:12px;line-height:18px;letter-spacing:-.06px;color:#bcbcbc
}
.pop-main .layer-foot .btn-close{right:-12px;top:-14px;background-position:-300px -275px
}
.list-mobile{margin-top:-32px
}
.list-mobile li{margin-bottom:12px
}
.list-mobile li:last-of-type{margin-bottom:0
}
.qr-chkin .qr-code{width:264px;margin:24px auto 20px;padding:12px;background:url(data:image/png;base64,) repeat 0 0
}
.qr-chkin .timer{font-size:16px;line-height:24px;letter-spacing:-.1px;text-align:center
}
.qr-chkin .timer span{color:#3617ce;font-weight:700
}
